
import request from "@/utils/request";


//  ()
export function getBlog(id) {
  return request({
    url: `/blogs/${id}`,
    method: "get",
  });
}


//  ()
export function getBlog_pages(type ="",page = 1) {
  return request({
    url: `/blogs?type=${type}&page=${page}`,
    method: "get",
  });
}


//
//  
export function getAllBlog(type ="",page=1) {
  return request({
    url: `/admin/blogs?type=${type}&page=${page}`,
    method: "get",
  });
}




// 
export function newBlog(data) {
  return request({
    url: `/admin/blogs`,
    method: "post",
    data: data
  });
}

// 
export function updateBlog(data) {
  console.log(data);
  return request({
    url: `admin/blogs/${data.id}`,
    method: "post",
    data: data
  });
}

// 
export function updateBlogImg(id,data) {
  console.log(data);
  return request({
    url: `admin/blogs/${id}`,
    method: "post",
    data: data
  });
}

// 
export function deleteBlog(id) {
  return request({
    url: `/admin/blogs/${id}`,
    method: "delete",
    data: {}
  });
}