<template>
  <div
    style="min-height: calc(100vh - 416px)"
    class="px-0 px-md-5 bg-white pb-5"
  >
    <loading :active.sync="isLoading"></loading>

    <div class="text-right mx-3 mx-md-0">
      <button
        v-if="daigouorder.status!=1 "
        class="btn btn-outline-primary mt-4"
        @click.prevent="openModal(true)"
      >
        
      </button>
    </div>
    <table class="table mt-4 mb-5 table-responsive" style="font-size: 1rem">
      <thead>
        <tr>
          <th class="text-nowrap"></th>
          <th class="text-nowrap text-center"></th>
          <th class="text-nowrap text-center"></th>
          <th class="text-nowrap text-center">()</th>
          <th class="text-nowrap text-center">(NTD)</th>
          <th class="text-nowrap text-center"></th>
          <th class="text-nowrap text-center"></th>
          <th v-if="daigouorder.status!=1 " class="text-center" width="120"></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="item in daigouitems" :key="item.id">
          <td class="text-nowrap">
            <a target="_blank" class="btn-link" :href="item.dgurl"></a>
          </td>
          <td class="text-nowrap text-center">
            <span v-if="item.dgtype.indexOf('name') > 0">{{
              JSON.parse(item.dgtype)["name"]
            }}</span>
            <span v-else>{{ item.dgtype }}</span>
          </td>
          <td class="text-nowrap text-center">
            {{ item.count }}
          </td>
          <td class="text-nowrap text-center"> {{ item.price }}</td>
          <td class="text-nowrap text-center">NTD {{ item.total }}</td>

          <td class="text-nowrap text-center">{{ item.up_at }}</td>
          <td class="text-nowrap text-center">{{ item.cr_at }}</td>

          <td v-if="daigouorder.status!=1 || $route.path.indexOf('/admin/orders')>-1" class="text-center text-nowrap">
            <button
              v-if="item.dgtype.indexOf('')==-1"
              class="btn btn-sm btn-outline-info mr-1"
              @click="openModal(false, item)"
            >
              
            </button>
            <button
            v-if="item.dgtype.indexOf('')==-1 || $route.path.indexOf('/admin/orders')>-1"
              class="btn btn-sm btn-outline-danger"
              @click="openDelModal(item)"
            >
              
            </button>
          </td>
        </tr>
      </tbody>
    </table>
    <div v-if="amountPrice == 0" class="text-center">
      
    </div>
    <hr class="mt-3" />
    <div v-if="amountPrice > 0" class="text-right mx-3 mx-md-0">
      <div>
        <h3 class="h3">:{{ amountPrice }}</h3>
      </div>
      <button
      v-if="daigouorder.status!=1 "
        class="btn btn-outline-primary mt-4"
        @click.prevent="daigou2cart()"
      >
        
      </button>
    </div>
    <!-- Bootstrap edit Modal -->
    <div
      class="modal fade"
      id="daigouitemModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
      data-backdrop="false"
    >
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-dark text-white">
            <h5 class="text-white modal-title" id="exampleModalLabel">
              <span v-if="isNew"></span>
              <span v-else></span>
            </h5>
            <!--  -->
            <button
              type="button"
              class="close"
              data-dismiss="modal"
              aria-label="Close"
            >
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col-12">
                <div class="form-group">
                  <label for="dgurl"></label>
                  <input
                    type="text"
                    class="form-control"
                    id="dgurl"
                    v-model="focusDaigouitems.dgurl"
                    placeholder=""
                  />
                </div>

                <div class="form-group">
                  <label for="title"></label>
                  <select
                    class="form-control"
                    v-model="focusDaigouitems.dgtypeId"
                  >
                    <template v-for="item in daigouparams">
                      <option
                        v-if="item.id != 1 || item.name != ''"
                        :key="item.id"
                        :value="item.id"
                      >
                        {{ item.name }} -> NTD {{ item.price }} 
                      </option>
                    </template>
                  </select>
                </div>

                <div class="form-group">
                  <label for="count"></label>
                  <input
                    type="number"
                    class="form-control"
                    id="count"
                    v-model.number="focusDaigouitems.count"
                    placeholder=""
                  />
                </div>

                <div class="form-group">
                  <label for="price"></label>
                  <input
                    type="number"
                    class="form-control"
                    id="price"
                    v-model.number="focusDaigouitems.price"
                    placeholder=""
                  />
                </div>

                <div class="form-group">
                  <label for="note"></label>
                  <p v-if="$route.path.indexOf('/admin/orders')>=-1" v-text="focusDaigouitems.note" style="white-space: pre-line;"/>
                  <textarea
                  v-if="$route.path.indexOf('/admin/orders')==-1"
                    rows="6"
                    type="text"
                    class="form-control"
                    id="note"
                    v-model="focusDaigouitems.note"
                    placeholder=""
                  ></textarea>
                </div>
                <div
                  v-if="daigouparams.length > 0 && totalPrice"
                  class="form-group"
                >
                  <label for="note"></label>
                  {{ focusDaigouitems.count }}  *  NTD
                  {{ dgtypePrice }}  +  
                  {{ focusDaigouitems.price }} / 
                  {{ daigouparams[0].price }}
                </div>
                <div v-if="totalPrice" class="form-group">
                  <label for="note"></label>
                  NTD <span>{{ totalPrice }}</span> 
                </div>

                <div v-if="!isNew" class="form-group">
                  <label for="note"></label>
                  NTD <span>{{ focusDaigouitems.total }}</span> 
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">

          <button
          v-if="$route.path.indexOf('/admin/orders')>=-1"
              type="button"
              class="btn btn-outline-secondary"
              @click.prevent="adminCloseModal()"
            >
              
            </button>
            <button
            v-else
              type="button"
              class="btn btn-outline-secondary"
              data-dismiss="modal"
            >
              
            </button>
            
            <button
              type="button"
              class="btn btn-primary"
              @click.prevent="updateDaigouitem()"
            >
              
            </button>
          </div>
        </div>
      </div>
    </div>
    <!-- delete Modal -->
    <div
      class="modal fade"
      id="deleteModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
      style="font-size: 1.6rem"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLabel"></h5>
            <button
              type="button"
              class="close"
              data-dismiss="modal"
              aria-label="Close"
            >
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body"></div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn btn-outline-secondary"
              data-dismiss="modal"
            >
              
            </button>
            <button
              type="button"
              class="btn btn-danger"
              @click="deleteDaigouitem()"
            >
              
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {
  getDaigouparams,
  getDaigouItems,
  setDaigouitem,
  deleteDaigouitem,
  updateDaigouitem,
  daigou2cart
} from "@/api/daigou";

// import ProductModal from "../modal/ProductModal";
// import Pagination from "../Pagination";
// import $ from "jquery";
// import axios from "axios";
export default {
  data() {
    return {
      dgid: this.$route.params.dgid,
      daigouitems: [],
      daigouparams: [],
      daigouorder: [],
      focusDaigouitems: {
        dgurl: "",
        dgtypeId: 2,
        count: 1,
        price: 0,
        note: "",
      },
      isNew: false, //
      isLoading: false,
    };
  },
  components: {
    // ProductModal
    // Pagination,
  },
  computed: {
    dgtypePrice() {
      let vm = this;
      if (vm.focusDaigouitems.dgtypeId) {
        const res = vm.daigouparams.find(function (item) {
          return item.id == vm.focusDaigouitems.dgtypeId; // 
        });
        if (res && res["price"]) {
          return res["price"];
        } else {
          return 0;
        }
      } else {
        return 0;
      }
    },
    totalPrice() {
      //
      let vm = this;
      if (
        vm.focusDaigouitems.count &&
        vm.dgtypePrice &&
        vm.focusDaigouitems.price &&
        vm.daigouparams.length > 0 &&
        vm.daigouparams[0].price
      ) {
        const price =
          vm.focusDaigouitems.count * vm.dgtypePrice +
          vm.focusDaigouitems.price / vm.daigouparams[0].price;
        return Math.round(price);
      } else {
        return 0;
      }
    },
    amountPrice() {
      //
      let vm = this;
      let ans = 0;
      if (vm.daigouitems && vm.daigouitems.length > 0) {
        vm.daigouitems.forEach((item) => {
          ans += item.total;
        });
        return ans;
      } else {
        return 0;
      }
    },
  },
  created() {
    this.getAllDaigouitems();
    this.getDaigouparams();
  },
  methods: {
    adminCloseModal(){
      window.$('#daigouitemModal').modal('hide');
    },
    getDaigouorder() {
      let vm = this;
      vm.isLoading = true;
      getDaigouparams().then((res) => {
        console.log(res);
        vm.daigouorder = res.data;
        vm.isLoading = false;
      });
    },
    getDaigouparams() {
      let vm = this;
      vm.isLoading = true;
      getDaigouparams().then((res) => {
        console.log(res);
        vm.daigouparams = res.data;
        vm.isLoading = false;
      });
    },

     daigou2cart() {
      let vm = this;
      vm.isLoading = true;
      daigou2cart(vm.dgid).then(() => {
        this.getAllDaigouitems();
        this.getDaigouparams();
        vm.isLoading = false;
        alert("");
      });
    },
    getAllDaigouitems() {
      let vm = this;
      vm.isLoading = true;
      getDaigouItems(vm.dgid).then((res) => {
        console.log(res);
        vm.daigouitems = res.data.list;
        vm.daigouorder = res.data.daigouorder;
        vm.isLoading = false;
      });
    },
    // 
    openModal(isNew, item) {
      console.log(isNew, item);
      if (isNew) {
        this.focusDaigouitems = {
          dgurl: "",
          dgtypeId: 2,
          count: 1,
          price: 0,
          note: "",
          status: 0,
        };
        this.isNew = true;
      } else {
        this.focusDaigouitems = {};
        this.focusDaigouitems = Object.assign({}, item);
        this.focusDaigouitems.dgtypeId = JSON.parse(
          this.focusDaigouitems.dgtype
        ).id;
        this.isNew = false;
      }
      
      window.$("#daigouitemModal").modal("show");
    },
    //
    openDelModal(item) {
      this.focusDaigouitems = Object.assign({}, item);
      window.$("#deleteModal").modal("show");
    },
    //
    updateDaigouitem() {
      let vm = this;
      if (!vm.isNew) {
        console.log("update");
        updateDaigouitem(vm.focusDaigouitems).then((response) => {
          if (response.data.success) {
            window.$("#daigouitemModal").modal("hide");
            vm.getAllDaigouitems();
            alert("");
          } else {
            vm.getAllDaigouitems();
            alert("" + response.data.error_Message[0]);
          }
        });
      } else {
        //
        setDaigouitem(vm.dgid, vm.focusDaigouitems).then((response) => {
          if (response.data.success) {
            window.$("#daigouitemModal").modal("hide");
            vm.getAllDaigouitems();
            alert("");
          } else {
            vm.getAllDaigouitems();
            alert("" + response.data.error_Message[0]);
          }
        });
      }
    },

    //
    deleteDaigouitem() {
      const vm = this;
      deleteDaigouitem(vm.focusDaigouitems.id).then((response) => {
        if (response.data.success) {
          window.$("#deleteModal").modal("hide");
          vm.getAllDaigouitems();
          alert("");
          // this.$bus.$emit("message:push", response.data.message, "success");
        } else {
          window.$("#deleteModal").modal("hide");
          vm.getAllDaigouitems();
          alert("");
        }
      });
    },
  },
};
</script>

