<template>
  <div>
    <Loading :active.sync="isLoading"></Loading>
    <div class="text-right">
      <button
        class="btn btn-outline-primary mt-4"
        @click.prevent="openModal(true)"
      >
        
      </button>
    </div>
    <table class="table mt-4">
      <thead>
        <tr>
          <th class="text-nowrap"></th>
          <th class="text-nowrap"></th>
          <th class="text-nowrap"></th>
          <th class="text-nowrap"></th>
          <th class="text-nowrap"></th>
          <th class="text-nowrap"></th>
        </tr>
      </thead>
      <tbody>
        <tr
          v-for="(item, key) in brands"
          :key="key"
          :class="{ 'text-secondary': !item.status === 'true' }"
        >
         <td>
            <ul>
              <li>{{ item.type }}</li>
            </ul>
          </td>
          <td>
            <ul>
              <li>{{ item.name }}</li>
              <li v-if="item.status==0" class="text-danger">()</li>
            </ul>
          </td>
       
          <td style="width:260px;"><img  :src="item.pic" v-if="item.pic" :alt="`${item.name}`" ></td>
      
          <td >
            <p style="white-space: pre-line;">
              {{ item.description }}
            </p>
          </td>
          <td>
            <button class="btn btn-primary btn-sm" @click="openModal(false, item)">
              
            </button>
          </td>
          <td>
             <button class="btn btn-danger btn-sm" @click="openDelModal(item)">
              
            </button>
          </td>
        </tr>
      </tbody>
    </table>

    <!-- <Pagination :compo-pages="pagination" @emitPages="getBrands"></Pagination> -->
    <!-- Bootstrap edit Modal -->
    <div
      class="modal fade"
      id="brandModel"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-dark text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span v-if="isNew"></span>
              <span v-else></span>
            </h5>
            <!--  -->
            <button
              type="button"
              class="close"
              data-dismiss="modal"
              aria-label="Close"
            >
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col-sm-4">
                <!-- <div class="form-group">
                  <label for="image"></label>
                  <input
                    type="text"
                    class="form-control"
                    id="image"
                    v-model="tempProduct.image"
                    placeholder=""
                  />
                </div> -->
                <div class="form-group" v-if="!isNew">
                  <label for="customFile">
                    
                    <i
                      class="fas fa-spinner fa-spin"
                      v-if="status.fileUploading"
                    ></i>
                  </label>
                  <input
                    type="file"
                    id="customFile"
                    class="form-control"
                    ref="files"
                    @change="uploadFile()"
                  />
                </div>
                <img  v-if="!isNew && !isLoading" class="img-fluid" :src="focusBrand.pic" alt />
              </div>
              <div class="col-sm-8">
                <div class="form-group">
                  <label for="title"></label>
                  <input
                    type="text"
                    class="form-control"
                    id="title"
                    v-model="focusBrand.name"
                    placeholder=""
                  />
                </div>
    

                <div class="form-group">
                  <label for="description"></label>
                  <textarea
                    rows="20"
                    type="text"
                    class="form-control"
                    id="description"
                    v-model="focusBrand.description"
                    placeholder=""
                  ></textarea>
                </div>

                 <div class="form-group">
                  <div class="form-check">
                    <!-- 10, 1true-value, 0 -->
                    <input
                      id="statuscheckbox"
                      class="form-check-input"
                      type="checkbox"
                      v-model="focusBrand.status"
                      :true-value="1"
                      :false-value="0"
                    />
                    <label for="statuscheckbox" class="form-check-label"></label>
                  </div>
                </div>
<hr>
                   <div class="form-group">
                  <div class="form-check">
                    <input v-model="focusBrand.type" class="form-check-input" type="radio" name="exampleRadios" id="exampleRadios1" value="" checked>
                    <label class="form-check-label" for="exampleRadios1">
                      
                    </label>
                  </div>
                  <div class="form-check">
                    <input v-model="focusBrand.type" class="form-check-input" type="radio" name="exampleRadios" id="exampleRadios2" value="">
                    <label class="form-check-label" for="exampleRadios2">
                      
                    </label>
                  </div>
           
                </div>
             
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn btn-outline-secondary"
              data-dismiss="modal"
            >
              
            </button>
            <button
              type="button"
              class="btn btn-primary"
              @click.prevent="updateBrand"
            >
              
            </button>
          </div>
        </div>
      </div>
    </div>
    <!-- delete Modal -->
    <div
      class="modal fade"
      id="deleteModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
      style="font-size: 1.6rem"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLabel"></h5>
            <button
              type="button"
              class="close"
              data-dismiss="modal"
              aria-label="Close"
            >
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            {{ focusBrand.name }}
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn btn-outline-secondary"
              data-dismiss="modal"
            >
              
            </button>
            <button
              type="button"
              class="btn btn-danger"
              @click="deleteBrand()"
            >
              
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// import $ from "jquery";
import { getBrand, newBrand, updateBrand, deleteBrand, uploadIMG } from "@/api/brand";

export default {
  data() {
    return {
      isNew:true,
      brands: {},
      isLoading: false,
      focusBrand:{},
      status: {
        fileUploading: false,
      },
    };
  },

  methods: {
    getBrands() {
      const vm = this;
      vm.isLoading = true;
      getBrand().then((response) => {
        vm.brands = response.data;
        vm.isLoading = false;
      });
    },
    // 
    openModal(isNew, item) {
      const vm =this;
      console.log(isNew, item);
      if (isNew) {
        vm.focusBrand = {};
        vm.isNew = true;
      } else {
        vm.focusBrand = Object.assign({}, item);
        vm.isNew = false;
      }
      window.$("#brandModel").modal("show");
     
    },

    //
    updateBrand() {
      let vm = this;
      console.log(vm.isNew);
      if (!vm.isNew) {
        // ,
        updateBrand(vm.focusBrand).then((response) => {
          window.$("#brandModel").modal("hide");
          if (response.data.success) {
            vm.getBrands();
            alert("");

          } else {
            vm.getBrands();
            alert("");
          }
        });
      } else {
        console.log("here ");
        //
        newBrand(vm.focusBrand).then((response) => {
          window.$("#brandModel").modal("hide");
          if (response.data.success) {
            vm.getBrands();
            alert("");
          } else {
            vm.getBrands();
             alert("");
          }
        });
      }
    },
        //(cors)
    uploadFile() {
      const uploadedFile = this.$refs.files.files[0];
      const vm = this;
      const formData = new FormData();
      formData.append("picfile", uploadedFile);
      vm.isLoading = true;
      uploadIMG(vm.focusBrand.id, formData).then((response) => {
         console.log(response);
          vm.isLoading = false;
          if (response.data.success) {
            console.log(response.data);
            vm.$set(vm.focusBrand, "pic", response.data.data.pic);
          }
      });

    },

    //
    openDelModal(item) {
      this.focusBrand = Object.assign({}, item);
      window.$("#deleteModal").modal("show");
    },
    //
    deleteBrand() {
      const vm = this;
      deleteBrand(vm.focusBrand).then((response) => {
        if (response.data.success) {
          window.$("#deleteModal").modal("hide");
          vm.getBrands();
          alert("");
          // this.$bus.$emit("message:push", response.data.message, "success");
        } else {
          window.$("#deleteModal").modal("hide");
          vm.getBrands();
          alert("");
        }
      });
    },

  },

  created() {
    this.getBrands();
    // console.log(process.env.APIPATH);
  },
};
</script>