import request from "@/utils/request";

//User


// 
export function getMyOrders() {
  return request({
    url: `/user/order`,
    method: "get",
  });
}


// 
export function createOrders(data) {
  return request({
    url: `/user/order`,
    method: "post",
    data: {
      name: data.name,
      email: data.email,
      phone: data.phone,
      address: data.address
    }
  });
}


//

// 
export function getOrder(currentPage = 1) {
  return request({
    url: `/admin/order?page=${currentPage}`,
    method: "get"
  });
}


// 
export function passOrder(ooid) {
  return request({
    url: `/admin/order/pass/${ooid}`,
    method: "post"
  });
}

// 
export function updateOrderTotal(ooid) {
  return request({
    url: `/admin/order/updatetotal/${ooid}`,
    method: "post"
  });
}

// 
export function payOrder(data) {
  return request({
    url: `/admin/order/pay`,
    method: "post",
    data: {
      ooid: data.ooid
    }
  });
}