import request from "@/utils/request";

// 
export function getCarts() {
  return request({
    url: `/user/cart`,
    method: "get"
  });
}

// 
export function deleteCarts(data) {
  return request({
    url: `/user/cart`,
    method: "delete",
    data: {
      ppid: data.ppid
    }
  });
}

// 
export function addCarts(data,count=1) {
  return request({
    url: `/user/cart`,
    method: "post",
    data: {
      ppid: data.ppid,
      count: count
    }
  });
}

// 
export function updateCarts(data) {
  return request({
    url: `/user/cart`,
    method: "put",
    data: {
      ppid: data.ppid,
      count: data.count
    }
  });
}